import React, { useEffect, useRef } from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  overflow: hidden;
`

const StyledVideo = styled.video`
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: #e3e3e3;
`

const Video = ({
  className,
  style,
  url,
  autoplay = 'true',
  loop = 'true',
  playsinline = 'true',
  poster,
  muted = 'true',
  onVisible,
  rootTopMargin = '100px',
  lazyLoad = true,
  preload,
}) => {
  const videoRef = useRef(null)

  useEffect(() => {
    const observer =
      window.IntersectionObserver &&
      new IntersectionObserver(
        ([entry]) => {
          if (entry.isIntersecting) {
            entry.target.src = url
            // entry.target.poster = poster
            entry.target.load()
            typeof onVisible === 'function' && onVisible(entry)
            observer.unobserve(videoRef.current)
          }
        },
        {
          root: null,
          rootMargin: `0px 0px ${rootTopMargin} 0px`,
          threshold: 0.1,
        },
      )
    if (observer && videoRef.current && lazyLoad) {
      observer.observe(videoRef.current)
    } else if ((!lazyLoad || !observer) && videoRef.current) {
      // eslint-disable-next-line immutable/no-mutation
      videoRef.current.src = url
      videoRef.current.load()
    }
    return () => {
      if (observer && lazyLoad) observer.unobserve(videoRef.current)
    }
  }, [videoRef])

  return (
    <Wrapper style={style} className={className}>
      <StyledVideo
        ref={videoRef}
        autoPlay={autoplay}
        loop={loop}
        playsInline={playsinline}
        muted={muted}
        data-src={url}
        preload={preload}
      />
    </Wrapper>
  )
}

export default Video
